<template>
  <div class="mycollection padd">
      <h1 class="large_t">我的收藏</h1>
      <ul class="clearfix">
          <li class="fl" v-for="i in 5" :key="i" @click="$router.push('/detail')">
              <img src="../../assets/test/goods.png" alt="">
              <p class="three_t_h name">尊享礼盒</p>
          </li>
          <p class="line"></p>
      </ul>
  </div>
</template>

<script>
export default {
  name: "Mycollection",
  data() {
    return {};
  },
  methods: {},
  components: {}
};
</script>

<style scoped lang="less">
.mycollection {
    font-size: 0.16rem;
    ul {
        position: relative;
        li {
            padding: 0.15rem 0rem;
            border-bottom: 0.01rem solid #f2f2f2;
            &:nth-child(3n + 2) {
                padding-left: 0.1rem;
                padding-right: 0.1rem;
            }
            img {
                width: 1rem;
                height: 1rem;
                display: block;
                background-size: cover;
                border-radius: 0.12rem;
            }
            .name {
                padding: 0.05rem 0rem;
            }

        }
        .line {
            height: 0.01rem;
            background-color: #ffffff;
            position: absolute;
            bottom: 0rem;
            left: 0rem;
            width: 100%;
        }
    }
}
</style>



